 #fff;\r\n  overflow-x: hidden !important;\r\n  font-weight: 400;\r\n}\r\n.theme-color {\r\n  color: #5b39c9;\r\n}\r\n.theme-bg {\r\n  background: #5b39c9;\r\n}\r\n/*1.2: background color*/\r\n\r\n/* 1.3: Buttons */\r\n.btn {\r\n  cursor: pointer;\r\n  z-index: 1;\r\n  position: relative;\r\n  padding: 16px 36px;\r\n  color: #5b39c9;\r\n  font-weight: 600;\r\n  text-transform: capitalize;\r\n  overflow: hidden;\r\n  border: 2px solid #5b39c9;\r\n  border-radius: 50px;\r\n  overflow: hidden;\r\n  -webkit-transition: 0.3s all linear;\r\n  transition: 0.3s all linear;\r\n  font-size: 14px;\r\n}\r\n\r\n.btn.active,\r\n.btn:hover,\r\n.btn:focus {\r\n  color: #fff;\r\n  text-decoration: none;\r\n}\r\n.btn:before {\r\n  content: \" \";\r\n  position: absolute;\r\n  top: 48%;\r\n  left: 48%;\r\n  z-index: -1;\r\n  -webkit-transform: scale(0);\r\n  transform: scale(0);\r\n  width: 11px;\r\n  height: 11px;\r\n  -webkit-transition: 0.3s all linear;\r\n  transition: 0.3s all linear;\r\n  opacity: 0;\r\n  visibility: hidden;\r\n  background: #5b39c9;\r\n  border-radius: 50px;\r\n}\r\n.btn:hover {\r\n  border-color: #5b39c9;\r\n}\r\n.btn:hover:before {\r\n  -webkit-transform: scale(20);\r\n  transform: scale(20);\r\n  opacity: 1;\r\n  visibility: visible;\r\n}\r\n\r\n.btn:focus,\r\n.form-control:focus {\r\n  box-shadow: none !important;\r\n}\r\n\r\n.disabled {\r\n  pointer-events: none;\r\n}\r\n\r\n.bg-2 {\r\n  background-color: #f7f6f9;\r\n}\r\nul,\r\nli {\r\n  margin-bottom: 0;\r\n  list-style: none;\r\n  padding: 0;\r\n}\r\n\r\n.font-weight-600 {\r\n  font-weight: 600;\r\n}\r\nb,\r\nstrong {\r\n  font-weight: bold;\r\n}\r\n.list-inline > li {\r\n  display: inline-block;\r\n}\r\n\r\n.form-control {\r\n  font-size: 14px;\r\n}\r\n.form-control:focus {\r\n  border-color: #fff;\r\n}\r\n\r\ninput.parsley-error,\r\ntextarea.parsley-error,\r\nselect.parsley-error {\r\n  border: 1px solid red !important;\r\n}\r\n.half-gutter {\r\n  margin-right: -7.5px;\r\n  margin-left: -7.5px;\r\n}\r\n.half-gutter > .col,\r\n.half-gutter > [class*=\"col-\"] {\r\n  padding-right: 7.5px;\r\n  padding-left: 7.5px;\r\n}\r\n.no-gutters-padding {\r\n  padding-right: 0px;\r\n  padding-left: 0px;\r\n}\r\n\r\n.theme-list-style {\r\n  padding: 0;\r\n  list-style: none;\r\n}\r\n.theme-list-style li {\r\n  font-weight: 600;\r\n  color: #4c4f60;\r\n  padding: 5px 0;\r\n}\r\n.theme-list-style li:before {\r\n  content: \"\\f05d\";\r\n  font-family: \"FontAwesome\";\r\n  color: #6f6c7f;\r\n  font-size: 12px;\r\n  font-weight: normal;\r\n  margin-right: 10px;\r\n}\r\n\r\n.theme-input-style {\r\n  font-size: 16px;\r\n  width: 100%;\r\n  height: 70px;\r\n  padding: 0 20px 0;\r\n  -webkit-transition: all 0.2s;\r\n  transition: all 0.2s;\r\n  background: transparent;\r\n  border: none;\r\n  border: 4px solid #f7f6f9;\r\n  opacity: 1;\r\n  border-radius: 10px;\r\n  color: #6f6c7f;\r\n  margin-bottom: 30px;\r\n}\r\n\r\ntextarea.theme-input-style {\r\n  padding: 10px;\r\n  min-height: 120px;\r\n  display: block;\r\n}\r\n\r\nlabel input[type=\"checkbox\"],\r\nlabel input[type=\"radio\"] {\r\n  position: relative;\r\n  top: 2px;\r\n  margin-right: 5px;\r\n}\r\n\r\n.form-group {\r\n  position: relative;\r\n}\r\n\r\n.table-bordered,\r\n.table-bordered td,\r\n.table-bordered th {\r\n  border-color: #e2e6fb;\r\n}\r\n\r\n.alignleft {\r\n  float: left;\r\n  margin-right: 15px;\r\n  margin-bottom: 15px;\r\n}\r\n.alignright {\r\n  float: right;\r\n  margin-left: 15px;\r\n  margin-bottom: 15px;\r\n}\r\n.aligncenter {\r\n  clear: both;\r\n  display: block;\r\n  margin: 0 auto 15px;\r\n}\r\n/* 1.4: Padding/Margin */\r\n.pt-155 {\r\n  padding-top: 155px;\r\n}\r\n.pb-155 {\r\n  padding-bottom: 155px;\r\n}\r\n.pt-150 {\r\n  padding-top: 150px;\r\n}\r\n.pb-150 {\r\n  padding-bottom: 150px;\r\n}\r\n.pt-140 {\r\n  padding-top: 140px;\r\n}\r\n.pb-140 {\r\n  padding-bottom: 140px;\r\n}\r\n.pt-130 {\r\n  padding-top: 130px;\r\n}\r\n.pb-130 {\r\n  padding-bottom: 130px;\r\n}\r\n.pt-120 {\r\n  padding-top: 120px;\r\n}\r\n.pt-115 {\r\n  padding-top: 115px;\r\n}\r\n.pb-115 {\r\n  padding-bottom: 115px;\r\n}\r\n.pb-120 {\r\n  padding-bottom: 120px;\r\n}\r\n.pt-110 {\r\n  padding-top: 110px;\r\n}\r\n.pb-110 {\r\n  padding-bottom: 110px;\r\n}\r\n.pt-100 {\r\n  padding-top: 100px;\r\n}\r\n.pb-100 {\r\n  padding-bottom: 100px;\r\n}\r\n.pt-90 {\r\n  padding-top: 90px;\r\n}\r\n.pb-90 {\r\n  padding-bottom: 90px;\r\n}\r\n.pt-80 {\r\n  padding-top: 80px;\r\n}\r\n.pb-80 {\r\n  padding-bottom: 80px;\r\n}\r\n.pt-70 {\r\n  padding-top: 70px;\r\n}\r\n.pb-70 {\r\n  padding-bottom: 70px;\r\n}\r\n.pt-60 {\r\n  padding-top: 60px;\r\n}\r\n.pb-60 {\r\n  padding-bottom: 60px;\r\n}\r\n.pt-55 {\r\n  padding-top: 55px;\r\n}\r\n.pb-55 {\r\n  padding-bottom: 55px;\r\n}\r\n.pt-50 {\r\n  padding-top: 50px;\r\n}\r\n.pb-50 {\r\n  padding-bottom: 50px;\r\n}\r\n.pt-30 {\r\n  padding-top: 30px;\r\n}\r\n.pb-30 {\r\n  padding-bottom: 30px;\r\n}\r\n.pt-20 {\r\n  padding-top: 20px;\r\n}\r\n.pb-20 {\r\n  padding-bottom: 20px;\r\n}\r\n\r\n.mt-140 {\r\n  margin-top: 140px;\r\n}\r\n.mb-140 {\r\n  margin-bottom: 140px;\r\n}\r\n.mt-120 {\r\n  margin-top: 120px;\r\n}\r\n.mb-120 {\r\n  margin-bottom: 120px;\r\n}\r\n.mt-70 {\r\n  margin-top: 70px;\r\n}\r\n.mb-70 {\r\n  margin-bottom: 70px;\r\n}\r\n.mt-60 {\r\n  margin-top: 60px;\r\n}\r\n.mb-60 {\r\n  margin-bottom: 60px;\r\n}\r\n.mt-50 {\r\n  margin-top: 50px;\r\n}\r\n.mb-50 {\r\n  margin-bottom: 50px;\r\n}\r\n.mt-40 {\r\n  margin-top: 40px;\r\n}\r\n.mb-40 {\r\n  margin-bottom: 40px;\r\n}\r\n.mt-30 {\r\n  margin-top: 30px;\r\n}\r\n.mb-30 {\r\n  margin-bottom: 30px;\r\n}\r\n\r\n/* 1.5: Animations */\r\n.animated {\r\n  -webkit-animation-duration: 1s;\r\n  animation-duration: 1s;\r\n  -webkit-animation-fill-mode: backwards;\r\n  animation-fill-mode: backwards;\r\n}\r\n.animated.infinite {\r\n  -webkit-animation-iteration-count: infinite;\r\n  animation-iteration-count: infinite;\r\n}\r\n\r\n@-webkit-keyframes fadeInDown {\r\n  0% {\r\n    opacity: 0;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\r\n    -webkit-transform: translate3d(0, -40px, 0);\r\n    transform: translate3d(0, -40px, 0);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\r\n    -webkit-transform: translateZ(0);\r\n    transform: translateZ(0);\r\n  }\r\n}\r\n@keyframes fadeInDown {\r\n  0% {\r\n    opacity: 0;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\r\n    -webkit-transform: translate3d(0, -40px, 0);\r\n    transform: translate3d(0, -40px, 0);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\r\n    -webkit-transform: translateZ(0);\r\n    transform: translateZ(0);\r\n  }\r\n}\r\n.fadeInDown {\r\n  -webkit-animation-name: fadeInDown;\r\n  animation-name: fadeInDown;\r\n}\r\n\r\n@-webkit-keyframes fadeInUp {\r\n  0% {\r\n    opacity: 0;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\r\n    -webkit-transform: translate3d(0, 40px, 0);\r\n    transform: translate3d(0, 40px, 0);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\r\n    -webkit-transform: translateZ(0);\r\n    transform: translateZ(0);\r\n  }\r\n}\r\n@keyframes fadeInUp {\r\n  0% {\r\n    opacity: 0;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\r\n    -webkit-transform: translate3d(0, 40px, 0);\r\n    transform: translate3d(0, 40px, 0);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\r\n    -webkit-transform: translateZ(0);\r\n    transform: translateZ(0);\r\n  }\r\n}\r\n.fadeInUp {\r\n  -webkit-animation-name: fadeInUp;\r\n  animation-name: fadeInUp;\r\n}\r\n\r\n@-webkit-keyframes fadeInLeft {\r\n  from {\r\n    opacity: 0;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\r\n    -webkit-transform: translate3d(-50%, 0, 0);\r\n    transform: translate3d(-50%, 0, 0);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\r\n    -webkit-transform: translate3d(0, 0, 0);\r\n    transform: translate3d(0, 0, 0);\r\n  }\r\n}\r\n@keyframes fadeInLeft {\r\n  from {\r\n    opacity: 0;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\r\n    -webkit-transform: translate3d(-50%, 0, 0);\r\n    transform: translate3d(-50%, 0, 0);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\r\n    -webkit-transform: translate3d(0, 0, 0);\r\n    transform: translate3d(0, 0, 0);\r\n  }\r\n}\r\n.fadeInLeft {\r\n  -webkit-animation-name: fadeInLeft;\r\n  animation-name: fadeInLeft;\r\n}\r\n\r\n@-webkit-keyframes fadeInRight {\r\n  from {\r\n    opacity: 0;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\r\n    -webkit-transform: translate3d(50%, 0, 0);\r\n    transform: translate3d(50%, 0, 0);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\r\n    -webkit-transform: translate3d(0, 0, 0);\r\n    transform: translate3d(0, 0, 0);\r\n  }\r\n}\r\n@keyframes fadeInRight {\r\n  from {\r\n    opacity: 0;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\r\n    -webkit-transform: translate3d(50%, 0, 0);\r\n    transform: translate3d(50%, 0, 0);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\r\n    -webkit-transform: translate3d(0, 0, 0);\r\n    transform: translate3d(0, 0, 0);\r\n  }\r\n}\r\n.fadeInRight {\r\n  -webkit-animation-name: fadeInRight;\r\n  animation-name: fadeInRight;\r\n}\r\n\r\n@-webkit-keyframes spin {\r\n  0% {\r\n    -webkit-transform: rotate(0deg);\r\n    transform: rotate(0deg);\r\n  }\r\n  100% {\r\n    -webkit-transform: rotate(360deg);\r\n    transform: rotate(3